
import yheader from '../components/ycheader'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'fundDetails',
data() {
return {
fundCode: '',
summary: {
dailyIncrease: 0,
updateDate: '',
value: 0,
oneMonth:0,
oneYear:0,
sevenDaysRate:0,
sinceFunding:0,
threeMonths:0,
threeYears:0,
totalNetValue:0,
updateDate:''
},
detail: {
fbiBuildDate:'',
fbiFundManager:'',
fbiLeable:'',
fbiPcfPoint:'',
fbiTrendLink:''
},
fundType: ''
}
},
computed: {

},
components: {
yheader,
Flexbox,
FlexboxItem
},
created() {
this.fundCode = this.$route.query.code;
this.getfundDetails(this.$route.query.code);
this.fundType = this.tool.getType(this.$route.query.type);
},
methods: {
getfundDetails(fundCode) {
let _this = this;
this.post({
url: "/fundMsg/queryFundDetail/v1.0",
data: {
fundCode: fundCode
},
success: function(e) {
if (e.code == "0000") {
_this.summary = e.result.summary;
_this.detail = e.result.detail;
}
}
})
},
colorType(e) {
if (e >= 0) {
return "_ff5255"
} else {
return "_36cca4"
}
}
}
}



